<template>
  <div>
    <Pop class="pop" v-bind="$attrs" v-on="$listeners" title="我的">
      <div class="mine-content" slot="content">
        <div class="qrcode" @click="showCard">
          我的二维码
          <img src="@/assets/images/mine/mine_qrcode@2x.png" alt="" />
        </div>
        <div class="user-info">
          <div :class="'hd level' + userInfo.leveal">
            <!-- <img :src="levelSrc" alt="" class="user-level" /> -->
            <img class="avator" :src="userAvator" />
            <!-- <img src="@/assets/images/mine/head@2x.png" alt="" /> -->
          </div>
          <div class="bd">
            <div class="name">{{ userInfo.nick_name }}</div>
            <div class="stars">
              <div class="own" @click="searchStar">
                <span class="label">获得星钻</span>
                <span class="num">{{ userInfo.total }}</span>
                <span class="link">赚取></span>
              </div>
              <div class="free" @click="give">
                <span class="label">可消费星钻</span>
                <span class="num">{{ userInfo.available }}</span>
                <span class="link">赠送好友></span>
              </div>
            </div>
          </div>
        </div>
        <div class="grids">
          <div class="grid" @click="goShop">
            <img src="@/assets/images/mine/mine_grid_shop@2x.png" alt="" />
            商店
          </div>
          <div class="grid" @click="goCoupon">
            <img src="@/assets/images/mine/mine_grid_coupon@2x.png" alt="" />
            我的商品
          </div>
          <div class="grid" @click="starDetail">
            <img src="@/assets/images/mine/mine_grid_star@2x.png" alt="" />
            星钻明细
          </div>
        </div>
      </div>
    </Pop>
    <Pop class="pop" :visible="isPopShow2" @hidePop="hidePop2" title="星钻明细">
      <div class="star-detail-content" slot="content">
        <div class="user-info pd-16">
          <div :class="'hd level' + userInfo.leveal">
            <img class="avator" :src="userAvator" />
          </div>
          <div class="bd">
            <div class="name">{{ userInfo.nick_name }}</div>
            <div class="stars">
              <div class="own">
                <span class="label">获得星钻</span>
                <span class="num">{{ userInfo.total }}</span>
              </div>
              <div class="free">
                <span class="label">可消费星钻</span>
                <span class="num">{{ userInfo.available }}</span>
              </div>
            </div>
          </div>
        </div>
        <div
          class="common-white-list mt-16"
          v-infinite-scroll="loadMore"
          infinite-scroll-disabled="loading"
          infinite-scroll-distance="10"
        >
          <div class="item" v-for="item in listData" :key="item.id">
            <div class="title mb-24">{{ item.desc }}</div>
            <div class="desc">{{ item.create_time }}</div>
            <span class="mark">
              <span :class="item.opt_num >= 0 ? 'add' : 'reduce'"
                >{{ item.opt_num >= 0 ? '+' : '' }}{{ item.opt_num }}星钻</span
              >
            </span>
          </div>
          <div class="item">
            <div class="title mb-24">体验虚拟会议室</div>
            <div class="desc">2022-04-08 05:18:00</div>
            <span class="mark">
              <span class="reduce">-10星钻</span>
            </span>
          </div>
          <div class="item">
            <div class="title mb-24">体验虚拟会议室</div>
            <div class="desc">2022-04-08 05:18:00</div>
            <span class="mark">
              <span class="add">+20星钻</span>
            </span>
          </div>
        </div>
      </div>
    </Pop>

    <EarnStar :visible.sync="isPopShow3" />

    <Modal class="modal" :visible.sync="isModalShow11" title="赠送好友">
      <div class="modal-content give-content" slot="content">
        <div class="edit-star">
          <div class="plus" @click="addStar"></div>
          <div class="inp"><input v-model="starCount" type="text" /></div>
          <div class="minus" @click="reduceStar"></div>
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="giveConfirm"
          src="@/assets/images/common/modal_btn_ljzs.png"
          alt=""
        />
      </div>
    </Modal>
    <Modal class="modal" :visible.sync="isModalShow12" title="我的名片">
      <div class="modal-content my-card" slot="content">
        <div class="user-info">
          <div :class="'hd level' + userInfo.leveal">
            <img class="avator" :src="userAvator" />
          </div>
          <div class="bd">
            <div class="name">{{ userInfo.nick_name }}</div>
            <div class="stars">
              <div class="own">
                <span class="label">获得星钻</span>
                <span class="num">{{ userInfo.total }}</span>
              </div>
              <div class="free">
                <span class="label">可消费星钻</span>
                <span class="num">{{ userInfo.available }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="ercode-section">
          <div class="ercode-box">
            <img src="@/assets/images/mine/mine_ercode.png" alt="" />
          </div>
        </div>
        <div class="tip">长按保存，邀请好友识别二维码参与活动</div>
      </div>
    </Modal>
  </div>
</template>
<script>
import Modal from '@/components/commonModal'
import Pop from '@/components/commonPop'
import EarnStar from '@/components/earnStar'

export default {
  name: 'Mine',
  props: {},
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      isPopShow2: false, // 星钻明细
      isPopShow3: false, // 探索信纪元
      isModalShow11: false, // 赠送好友
      isModalShow12: false, // 我的名片
      starCount: 66,
      giveLoading: false, // 赠送提交中
      pageNum: 1,
      pageSize: 20,
      loading: false,
      isLoadedAll: false,
      listData: []
    }
  },
  components: {
    Modal,
    Pop,
    EarnStar
  },
  computed: {
    userAvator () {
      return `/static/img/user/head${this.userInfo.icon_type}@2x.png`
    }
  },
  watch: {
    $router () {
      console.log('attr', this.$attrs)
    }
  },
  methods: {
    // 赠送好友
    give () {
      this.isModalShow11 = true
    },
    // 我的名片
    showCard () {
      this.$router.push('personalPage')
      // this.isModalShow12 = true
    },
    addStar () {
      // 最多9999
      if (Number(this.starCount) < 9999) {
        this.starCount++
      }
    },
    reduceStar () {
      // 最少1
      if (Number(this.starCount) > 1) {
        this.starCount--
      }
    },
    goShop () {
      this.$router.push('shop')
    },
    goCoupon () {
      this.$router.push('coupon')
    },
    // 星钻明细
    async starDetail () {
      const res = await this.$request('post', '/user/getaccountlogpagelist', {
        uid: this.userInfo.uid,
        page_num: this.pageNum,
        page_size: this.pageSize
      })
      const list = res.data.list || []
      this.listData = [...this.listData, ...list]
      this.isPopShow2 = true
    },
    async loadMore () {
      if (this.isLoadedAll) {
        return
      }
      this.loading = true
      this.pageNum++
      const res = await this.$request('post', '/user/getaccountlogpagelist', {
        uid: this.userInfo.uid,
        page_num: this.pageNum,
        page_size: this.pageSize
      })
      const list = res.data.list || []
      this.listData = [...this.listData, ...list]
      if (res.data.list.length === 0) {
        this.isLoadedAll = true
      }
      this.loading = false
    },
    searchStar () {
      // this.isPopShow = false
      this.isPopShow3 = true
    },
    hidePop2 () {
      this.isPopShow2 = false
    },
    // 确认赠送
    async giveConfirm () {
      if (this.giveLoading) {
        return
      }
      this.giveLoading = true
      await this.$request('post', '/user/givefriends', {
        uid: this.userInfo.uid,
        num: this.starCount
      }).finally(() => {
        this.giveLoading = false
      })
      this.Toast('赠送成功')
      this.isModalShow11 = false
    },

    cancel () {},
    confirm () {}
  }
}
</script>
<style lang="less" scoped>
.mine-content {
  position: relative;
  padding: 16px;
  .qrcode {
    font-size: 20px;
    color: #333333;
    padding: 16px 20px;
    background-color: #fff;
    border-radius: 0px 16px 0px 16px;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    img {
      margin-left: 5px;
      width: 24px;
      height: 24px;
    }
  }
  .user-info {
    padding: 32px 32px 28px;
    display: flex;
    align-items: center;
    .bd {
      margin-left: 16px;
      .name {
        color: #333333;
        font-size: 32px;
        margin-bottom: 16px;
      }
      .stars {
        font-size: 20px;
        display: flex;
        .own,
        .free {
          background-color: #fff;
          border-radius: 16px;
          padding: 10px 15px;
          .label {
            color: #333333;
          }
          .num {
            color: #fe492a;
          }
          .link {
            color: #999999;
          }
        }
        .own {
          margin-right: 16px;
        }
      }
    }
  }
  .grids {
    background-color: #fff;
    border-radius: 16px;
    display: flex;
    justify-content: space-around;
    padding: 40px 10px;
    .grid {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 24px;
      color: #333333;
      img {
        width: 80px;
        height: 80px;
      }
    }
  }
}
.my-card {
  text-align: left;
  .user-info {
    display: flex;
    align-items: center;
    .bd {
      margin-left: 16px;
      .name {
        color: #333333;
        font-size: 32px;
        margin-bottom: 20px;
      }
      .stars {
        font-size: 20px;
        display: flex;
        .own,
        .free {
          .label {
            color: #333333;
          }
          .num {
            color: #fe492a;
          }
          .link {
            color: #999999;
          }
        }
        .own {
          margin-right: 16px;
        }
      }
    }
  }
  .ercode-section {
    margin-top: 18px;
    margin-bottom: 16px;
    text-align: center;
    .ercode-box {
      font-size: 0;
      padding: 20px;
      border: 2px solid #feeebc;
      border-radius: 20px;
      display: inline-block;
      img {
        width: 180px;
        height: 180px;
      }
    }
  }

  .tip {
    color: #666666;
    font-size: 20px;
    text-align: center;
  }
}
.give-content {
  .edit-star {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 70px;
    .plus,
    .minus {
      width: 72px;
      height: 72px;
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: center;
    }
    .plus {
      background-image: url(~@/assets/images/mine/mine_plus@2x.png);
    }
    .minus {
      background-image: url(~@/assets/images/mine/mine_minus@2x.png);
    }
    .inp {
      border-bottom: 4px solid #feeebc;
      width: 254px;
      input {
        height: 80px;
        width: 100%;
        border: none;
        outline: none;
        font-size: 80px;
        color: #333;
        text-align: center;
      }
    }
  }
}
.star-detail-content {
  padding: 16px;
  .user-info {
    display: flex;
    align-items: center;
    .bd {
      margin-left: 16px;
      .name {
        color: #333333;
        font-size: 32px;
        margin-bottom: 20px;
      }
      .stars {
        font-size: 20px;
        display: flex;
        .own,
        .free {
          .label {
            color: #333333;
          }
          .num {
            color: #fe492a;
          }
          .link {
            color: #999999;
          }
        }
        .own {
          margin-right: 16px;
        }
      }
    }
  }
  .common-white-list {
    & > .item {
      background-color: #fff;
      padding: 32px;
      border-radius: 16px;
      margin-bottom: 16px;
      position: relative;
      .title {
        font-size: 32px;
        color: #333333;
        font-weight: 600;
      }
      .desc {
        font-size: 24px;
        color: #666666;
      }
      .mark {
        position: absolute;
        right: 32px;
        top: 32px;
        font-size: 32px;
        .add {
          color: #ff4022;
        }
        .reduce {
          color: #666666;
        }
      }
    }
  }
}
.user-info {
  .hd {
    flex: 0 0 96px;
    width: 96px;
    height: 96px;
    box-sizing: border-box;
    padding: 4px;
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
    &.level0 {
      background-image: url(/static/img/user/level0@2x.png);
    }
    &.level1 {
      background-image: url(/static/img/user/level1@2x.png);
    }
    &.level2 {
      background-image: url(/static/img/user/level2@2x.png);
    }
    &.level3 {
      background-image: url(/static/img/user/level3@2x.png);
    }
    &.level4 {
      background-image: url(/static/img/user/level4@2x.png);
    }
    &.level5 {
      background-image: url(/static/img/user/level5@2x.png);
    }
  }
}
</style>



