<template>
  <div v-if="visible" class="sign">
    <div class="mask"></div>
    <div class="main" v-if="!showResult">
      <div class="title">
        <img src="@/assets/images/sign/sign_title@2x.png" alt="" />
      </div>
      <div class="close" @click="hideModal">
        <img src="@/assets/images/sign/sign_close@2x.png" alt="" />
      </div>
      <div class="stars">
        <img src="@/assets/images/sign/sign_bg@2x.png" alt="" />
      </div>
      <div :class="isShaking ? 'ball animated' : 'ball'">
        <img src="@/assets/images/sign/sign_ball@2x.png" alt="" />
        <!-- <canvas class="canvas" id="pag"></canvas> -->
      </div>
      <div class="btn" @click="shake">
        <img src="@/assets/images/sign/sign_btn@2x.png" alt="" />
      </div>
    </div>
    <div class="result" v-else>
      <div class="ani">
        <div class="title">每日一签</div>
        <div class="text">
          <div>太棒了！</div>
          <div>您已经累计签到{{ signResult.total_sign }}天～</div>
        </div>
        <div class="content">
          <div class="content-text">信纪元</div>
          <div class="content-no">No.{{ signResult.code }}</div>
          <img :src="signContentUrl" alt="" />
        </div>
        <div class="light">
          <img src="@/assets/images/sign/light@2x.png" alt="" />
        </div>
        <div class="result-text">
          <span class="label">签到奖励：</span>
          <span class="num">+{{ signResult.reward }}</span>
          <img src="@/assets/images/sign/star@2x.png" alt="" />
        </div>
      </div>
      <div class="close" @click="hideModal">
        <img src="@/assets/images/sign/sign_close@2x.png" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
import img1 from '@/assets/images/sign/content_1@2x.png'
import img2 from '@/assets/images/sign/content_2@2x.png'
import img3 from '@/assets/images/sign/content_3@2x.png'
import img4 from '@/assets/images/sign/content_4@2x.png'
import img5 from '@/assets/images/sign/content_5@2x.png'
export default {
  name: 'Sign',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      showResult: false,
      isShaking: false,
      isBtnLoading: false, // 防重复点击
      signResult: null
    }
  },
  watch: {
    visible (n) {
      if (n) {
        this.showResult = false
      }
    }
  },
  computed: {
    signContentUrl () {
      if (this.signResult.code === '1') {
        return img1
      } else if (this.signResult.code === '2') {
        return img2
      } else if (this.signResult.code === '3') {
        return img3
      } else if (this.signResult.code === '4') {
        return img4
      } else if (this.signResult.code === '5') {
        return img5
      }
    }
  },
  mounted () {},
  methods: {
    hideModal () {
      this.$emit('update:visible', false)
    },
    async shake () {
      if (this.isBtnLoading) {
        return
      }
      const res = await this.$request('post', '/user/sign', {
        uid: this.userInfo.uid
      })
      this.signResult = res.data
      this.isBtnLoading = true
      this.isShaking = true
      setTimeout(() => {
        this.isBtnLoading = false
        this.isShaking = false
        this.showResult = true
      }, 2000)
    },
    async play () {
      await this.pagView.play()
    }
  }
}
</script>

<style lang="less" scoped>
.hide {
  display: none;
}
.sign {
  position: fixed;
  z-index: 999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  .mask {
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
  }
  .main {
    .title {
      position: absolute;
      top: 150px;
      left: 50%;
      transform: translateX(-50%);
      width: 318px;
      height: 62px;
      z-index: 9999;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .close {
      position: absolute;
      right: 90px;
      top: 250px;
      width: 67px;
      height: 67px;
      z-index: 9999;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .stars {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 330px;
      width: 548px;
      height: 597px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .ball {
      position: absolute;
      left: 50%;
      margin-left: -374px;
      top: 210px;
      width: 748px;
      height: 906px;
      img {
        width: 100%;
        height: 100%;
      }
      &.animated {
        animation: rockit 0.3s ease infinite alternate;
      }
      @keyframes rockit {
        0% {
          transform: rotate(-35deg);
        }
        100% {
          transform: rotate(10deg);
        }
      }
    }
    .btn {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 1000px;
      width: 338px;
      height: 84px;
      z-index: 9999;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .result {
    position: relative;
    z-index: 99;
    .ani {
      width: 514px;
      height: 1054px;
      background-image: url(~@/assets/images/sign/result_bg@2x.png);
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: center;
      position: relative;
      left: 50%;
      margin-left: -257px;
      text-align: center;
      .title {
        width: 514px;
        text-align: center;
        position: absolute;
        top: 285px;
        color: #d83834;
        font-size: 39.6px;
        font-weight: 600;
        letter-spacing: 37px;
        margin-left: 18px;
      }
      .text {
        width: 514px;
        text-align: center;
        position: absolute;
        top: 382px;
        color: #fff2bc;
        font-size: 34px;
        line-height: 48px;
      }
      .content {
        position: absolute;
        top: 504px;
        left: 50%;
        transform: translateX(-50%);
        width: 404px;
        height: 268px;
        .content-text {
          position: absolute;
          right: 34px;
          font-size: 36px;
          writing-mode: vertical-lr;
          color: #c70000;
          top: 50%;
          transform: translateY(-50%);
          letter-spacing: 10px;
          height: 100%;
        }
        .content-no {
          position: absolute;
          bottom: 12px;
          font-size: 16px;
          color: #c70000;
          left: 16px;
        }
        img {
          width: 100%;
          height: 100%;
        }
      }
      .light {
        width: 750px;
        background-image: url(~@/assets/images/sign/light@2x.png);
        background-size: 782px 561px;
        position: absolute;
        top: 74px;
        background-repeat: no-repeat;
        z-index: -1;
        margin-left: -120px;
      }
      .result-text {
        width: 404px;
        left: 50%;
        transform: translateX(-50%);
        position: absolute;
        top: 806px;
        .label {
          font-size: 24px;
          color: #fff2bc;
        }
        .num {
          font-size: 40px;
          color: #fff2bc;
        }
        img {
          width: 32px;
          height: 28px;
        }
      }
    }
    .close {
      position: absolute;
      left: 50%;
      bottom: -100px;
      width: 67px;
      height: 67px;
      z-index: 9999;
      transform: translateX(-50%);
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>



