<template>
  <!-- 获取星钻弹窗 -->
    <Pop
      class="pop"
      :visible="visible"
      @hidePop="hidePop3"
      title="探索信纪元，集星钻好礼"
    >
      <div class="search-star-content" slot="content">
        <div class="pop-section-title mb-16">初步探索</div>
        <div class="common-white-list mt-16">
          <div class="item">
            <div class="title mb-24">探索投资知识</div>
            <div class="desc">+20星钻，每浏览一次投教页面</div>
            <span class="mark">
              <span class="btn-gray">已完成</span>
            </span>
          </div>
          <div class="item">
            <div class="title mb-24">体验虚拟会议室</div>
            <div class="desc">+50星钻，每体验1次虚拟会议室，每日最多1次</div>
            <span class="mark">
              <span class="btn-red">去探索</span>
            </span>
          </div>
          <div class="item">
            <div class="title mb-24">邀请好友</div>
            <div class="desc">+50星钻，每邀请1位好友参与活动，每日不限次数</div>
            <span class="mark">
              <span class="btn-red">去邀请</span>
            </span>
          </div>
        </div>
        <div class="pop-section-title mb-16">深入探索</div>
        <div class="common-white-list mt-16">
          <div class="item">
            <div class="title mb-24">首次开通OTC权限</div>
            <div class="desc">+100星钻</div>
            <span class="mark">
              <span class="btn-red">去探索</span>
            </span>
          </div>
          <div class="item">
            <div class="title mb-24">资产小达人</div>
            <div class="desc">+200星钻，账户资产达到10万</div>
            <span class="mark">
              <span class="btn-red">去探索</span>
            </span>
          </div>
        </div>
      </div>
    </Pop>
</template>

<script>
import Pop from '@/components/commonPop'
export default {
  name: 'EarnStar',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Pop
  },
  methods: {
    hidePop3 () {
      this.$emit('update:visible', false)
    }
  }
}
</script>

<style lang="less" scoped>
.search-star-content {
  padding: 30px;
  .pop-section-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
    color: #daaf4a;
    position: relative;
    &::before,
    &::after {
      content: ' ';
      display: inline-block;
      width: 100px;
      height: 32px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100%;
      margin: 0 10px;
    }
    &::before {
      background-image: url(~@/assets/images/mine/wave_l@2x.png);
    }
    &::after {
      background-image: url(~@/assets/images/mine/wave_r@2x.png);
    }
  }
  .common-white-list {
    & > .item {
      background-color: #fff;
      padding: 32px;
      border-radius: 16px;
      margin-bottom: 16px;
      position: relative;
      .title {
        font-size: 32px;
        color: #333333;
        font-weight: 600;
      }
      .desc {
        font-size: 24px;
        color: #666666;
      }
      .mark {
        .add,
        .reduce {
          position: absolute;
          right: 32px;
          top: 32px;
          font-size: 32px;
        }
        .add {
          color: #ff4022;
        }
        .reduce {
          color: #666666;
        }
        .btn-gray,
        .btn-red {
          position: absolute;
          right: 30px;
          top: 20px;
          width: 174px;
          height: 64px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 100%;
          text-align: center;
          line-height: 52px;
          display: inline-block;
          font-size: 22px;
        }
        .btn-gray {
          color: #d6d6d6;
          background-image: url(~@/assets/images/mine/gray_blank_btn@2x.png);
        }
        .btn-red {
          color: #ffe29d;
          background-image: url(~@/assets/images/mine/red_blank_btn@2x.png);
        }
      }
    }
  }
}
</style>



