<template>
  <div class="page home-page" ref="homePage" id="homePage">
    <div class="cont">
      <div class="info">
        <div class="info-left">
          <user-info></user-info>
          <div class="notice align-center" @click="showNotice">
            <img src="../assets/home/notice@2x.png" alt="" class="horn" />
            <div class="text notice-animation">
              {{ lastestNotice }}
            </div>
          </div>
        </div>
        <div class="info-right">
          <div class="rbox share">
            <img
              src="../assets/home/share@2x.png"
              alt=""
              class="rimg share-img"
            />
          </div>
          <div class="rbox strategy">
            <img
              src="../assets/home/strategy@2x.png"
              alt=""
              class="rimg strategy-img"
              @click="$router.push('strategy')"
            />
          </div>
        </div>
      </div>
      <div class="group pr">
        <div class="store" @click="goto('shop')">
          <img
            v-show="!dyShop"
            src="../assets/home/map_shop@2x.png"
            class="space-img space-shop"
          />
          <div v-show="dyShop" id="dyShop"></div>
        </div>
        <div class="meeting" @click="goto('meeting')">
          <!-- <div class="tit">动画上文字test</div> -->
          <img
            v-show="!dyMeeting"
            src="../assets/home/map_meeting@2x.png"
            alt=""
            class="space-img space-meeting"
          />
          <div v-show="dyMeeting" id="dyMeeting"></div>
        </div>
        <div class="digital" @click="goto('digitalCollection')">
          <div v-show="dyDigital" id="dyDigital"></div>
          <img
            src="../assets/home/map_digital@2x.png"
            v-show="!dyDigital"
            class="space-img space-digital"
          />
        </div>
        <div class="dream" @click="goto('dream')">
          <img
            src="../assets/home/map_dream@2x.png"
            v-show="!dyDream"
            class="space-img space-dream"
          />
          <div v-show="dyDream" id="dyDream"></div>
        </div>
        <div class="wait">
          <img
            src="../assets/home/map_wait@2x.png"
            alt=""
            class="space-img space-wait"
          />
        </div>
        <div class="waiting">
          <img
            src="../assets/home/map_waiting@2x.png"
            alt=""
            class="space-img space-waiting"
          />
        </div>
      </div>
      <div class="sort-box" @click="goSort"></div>
      <div class="tabs align-end">
        <div class="tab-box">
          <div class="tab-nav signin" @click="showSign">
            <img src="../assets/home/signin@2x.png" />
          </div>
          <div class="tab-nav collect-star" @click="searchStar">
            <img src="../assets/home/collect_star@2x.png" />
          </div>
          <div class="tab-nav mine" @click="showMine">
            <img src="../assets/home/mine@2x.png" />
          </div>
        </div>
      </div>
    </div>

    <!-- 签到 -->
    <Sign :visible.sync="isSignShow" />
    <!-- 获取星钻 -->
    <earnStar :visible.sync="isPopShow3" />
    <!-- 我的 -->
    <Mine :visible="isPopShow" @hidePop="hidePop" />

    <Pop class="pop" :visible.sync="isPopNotice" title="公告">
      <div class="star-detail-content" slot="content">
        <div class="common-white-list mt-16">
          <div class="item" v-for="(item, index) in noticeList" :key="index">
            <div class="title mb-24">{{ item.title }}</div>
            <div class="desc">{{ item.content }}</div>
            <span class="mark">
              <span class="time">{{ item.create_time }}</span>
            </span>
          </div>
          <!-- <div class="item">
            <div class="title mb-24">活动上新</div>
            <div class="desc">数字藏品空间上喜欢你啦~ 24节气信小福</div>
            <span class="mark">
              <span class="time">2022-04-08 05:18:00</span>
            </span>
          </div> -->
        </div>
      </div>
    </Pop>

    <!-- 动效进入 -->
    <div class="mask-layer" v-show="showAnimatIn">
      <div class="mask-con">
        <div class="mask-bg animation3">
          <img src="~@/assets/home/butterfly@2x.png" />
        </div>
        <div class="content">
          <div class="tit">
            <img src="~@/assets/home/welcome@2x.png" />
          </div>
          <div class="circle-wrap">
            <div class="circle1 animation2"></div>
            <div class="circle2 animation1"></div>
            <div class="circle3">
              <div class="head_level">
                <img :src="levelSrc" class="bg" />
                <!-- <img class="head" src="~@/assets/home/head1@2x.png" /> -->
                <img class="head" :src="headSrc" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="mask-btn">
        <img @click="hideAnimitIn" src="../assets/home/confirm_btn@2x.png" />
      </div>
    </div>

    <!-- 获得好友赠礼 -->
    <Modal class="modal" :visible.sync="isModalReceived" title="好友赠礼">
      <div class="modal-content" slot="content">
        <div class="text mb-24">恭喜获得好友赠送的{{ receive_amount }}星钻</div>
        <div class="sub-text mb-9">invite_amount</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="hideReceived"
          src="@/assets/images/common/modal_btn_zdl.png"
          alt=""
        />
      </div>
    </Modal>

    <!-- 邀请成功 -->
    <Modal class="modal" :visible.sync="isModalInvite" title="恭喜您">
      <div class="modal-content" slot="content">
        <div class="text mb-24">成功唤来{{ invite_count }}位朋友</div>
        <div class="sub-text mb-9">星钻+{{ invite_amount }}</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="hideInvite"
          src="@/assets/images/common/modal_btn_zdl.png"
          alt=""
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import UserInfo from '@/components/userInfo'
// import meetingJson from '@/assets/json/meeting' // 引入json文件
import meetingJson from '../../static/animation/home_meeting/data' // 引入json文件
import shopJson from '../../static/animation/home_shop/data' // 引入json文件
import dreamJson from '../../static/animation/home_dream/data' // 引入json文件
import digitalJson from '../../static/animation/home_digital/data' // 引入json文件
import Mine from '@/components/mine'
import Sign from '@/components/sign'
import EarnStar from '@/components/earnStar'

export default {
  components: {
    UserInfo,
    Mine,
    Sign,
    EarnStar
  },

  data () {
    return {
      userInfo: this.$store.getters.userInfo,
      lastestNotice: '',
      number: 0,
      noticeTimer: null,
      dyMeeting: true, // 是否显示虚拟会议动画
      dyShop: true, // 是商店动画
      dyDigital: true, //
      dyDream: true, //
      isPopNotice: false, // 公告弹窗
      isPopShow: false, // 我的弹窗
      isSignShow: false, // 签到弹窗
      isPopShow3: false, // 探索信纪元弹窗
      isModalReceived: false, // 收到好友赠礼弹窗
      isModalInvite: false, // 邀请成功弹窗
      showAnimatIn: false,
      // headSrc: './static/img/user/head' + JSON.parse(localStorage.getItem('userInfo')).data.icon_type + '@2x.png',
      // levelSrc: './static/img/user/level' + JSON.parse(localStorage.getItem('userInfo')).data.leveal + '@2x.png'
      headSrc: './static/img/user/head' + this.$store.getters.userInfo.role + '@2x.png',
      levelSrc: './static/img/user/level' + this.$store.getters.userInfo.leveal + '@2x.png',
      noticeList: [],
      receive_amount: 0, // 收到好友赠送数量
      invite_count: 0, // 邀请人数
      invite_amount: 0 // 邀请奖励数量
    }
  },
  computed: {
    text () {
      return {
        id: this.number,
        val: this.textArr[this.number]
      }
    }
  },
  mounted () {
    const params = {
      container: document.getElementById('dyMeeting'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: meetingJson
    }
    this.anim = this.lottie.loadAnimation(params)

    const paramsShop = {
      container: document.getElementById('dyShop'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: shopJson
    }
    this.anim = this.lottie.loadAnimation(paramsShop)
    const paramsDigital = {
      container: document.getElementById('dyDigital'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: digitalJson
    }
    this.anim = this.lottie.loadAnimation(paramsDigital)

    const paramsDream = {
      container: document.getElementById('dyDream'),
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: dreamJson
    }
    this.anim = this.lottie.loadAnimation(paramsDream)

    // console.log(JSON.parse(localStorage.getItem('userInfo')).data)

    // 生成头像后进入显示进入动效
    let first = this.$route.params.headIndex
    // console.log(first)
    if (first) {
      this.showAnimatIn = true
    }

    // 二级页面返回home，显示赚取星钻弹窗
    // let earnStar = this.$route.params.earnStar
    // if (earnStar === '1') {
    //   this.isPopShow = false
    //   this.isPopShow3 = true
    // }
  },
  watch: {
    $route (to, from) {
      // 监听路由变化, 实现类似 小程序的 onShow 事件
      if (to.name === 'home') {
        this.isPopShow = false
        let earnStar = to.params.earnStar
        if (earnStar === '1') {
          this.isPopShow3 = true
        }
      }
    }
  },
  created () {
    // this.login()
    this.getLastestNotice()
    this.getNoticeList()
    this.getInvitenotice()
    this.getBaseInfo()
  },
  methods: {
    startMove () {
      let that = this
      // eslint-disable-next-line
      that.noticeTimer = setTimeout(() => {
        if (this.number === this.textArr.length) {
          this.number = 0
        } else {
          this.number += 1
        }
        clearTimeout(that.noticeTimer)
        this.startMove()
      }, 2500) // 滚动不需要停顿则将2000改成动画持续时间
    },
    goSort () {
      this.$router.push('invitationSortList')
    },
    goto (name, params) {
      // this.$router.push({ name: name, params: { headIndex: this.userActive } })
      this.$router.push({ name: name, params: params })
    },
    showNotice () {
      this.isPopNotice = true
    },
    // 展示我的弹窗
    showMine () {
      this.isPopShow = true
    },
    // 隐藏我的弹窗
    hidePop () {
      this.isPopShow = false
    },
    // 签到弹窗
    showSign () {
      this.isSignShow = true
    },
    // 获取星钻弹窗
    searchStar () {
      this.isPopShow3 = true
    },
    hideAnimitIn () {
      this.showAnimatIn = false
    },

    // 最新公告
    async getLastestNotice () {
      let res = await this.$request('get', 'activity/getlatestnotice')
      // console.log('notice', res)
      if (res && res.error_no === '0') {
        this.lastestNotice = res.data.title + ' : ' + res.data.content
      }
    },
    // 公告列表
    async getNoticeList () {
      let res = await this.$request('get', 'activity/getnoticelist')
      // console.log('noticeList', res)
      if (res && res.error_no === '0') {
        this.noticeList = res.data
      }
    },
    // 首页奖励信息弹窗
    async getInvitenotice () {
      let res = await this.$request('get', 'user/getinvitenotice', { uid: this.userInfo.uid })
      if (res && res.error_no === '0') {
        // this.noticeList = res.data
        console.log(res.data)
        // 进入活动主页调用，invite_count>0 显示邀请好友奖励弹窗，receive_amount>0显示收到好友赠送弹窗，两个都>0 先显示赠送弹窗，关闭后显示邀请弹窗
        if (res.data.receive_amount > 0) {
          this.isModalReceived = true
        }
      }
    },
    hideReceived () {
      this.isModalReceived = false
      if (this.invite_count > 0) {
        this.isModalInvite = true
      }
    },
    hideInvite () {
      this.isModalInvite = false
    },
    // 获取活动基本信息,分享配置数据
    async getBaseInfo () {
      let res = await this.$request('get', 'activity/getbaseinfo', {})
      if (res && res.error_no === '0') {
        console.log(res.data)
        localStorage.setExpire('baseInfo', JSON.stringify(res.data))
      }
    }
  }
}
</script>

