.animation1 {
  z-index: 1;
  opacity: 0;
  -webkit-animation: warn 1.8s ease-out;
  -moz-animation: warn 1.8s ease-out;
  animation: warn 1.8s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.animation2 {
  z-index: 1;
  opacity: 0;
  -webkit-animation: wai1 1.5s ease-out;
  -moz-animation: wai1 1.5s ease-out;
  animation: wai1 1.5s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
.animation3 {
  animation: ymove 1.8s infinite;
}

.notice-animation {
  -webkit-animation: 10s rowup linear infinite normal;
  animation: 10s rowup linear infinite normal;
  position: relative;
}

@keyframes warn {
  0% {
    transform: scale(0.8);
    opacity: 0.3;
  }
  25% {
    transform: scale(0.85);
    opacity: 0.65;
  }
  50% {
    transform: scale(0.9);
    opacity: 1;
  }
  75% {
    transform: scale(1);
    opacity: 0.75;
  }
  100% {
    transform: scale(1.3);
    opacity: 0.5;
  }
}
@keyframes wai1 {
  0% {
    transform: scale(0.5);
    opacity: 0.3;
  }
  25% {
    transform: scale(0.85);
    opacity: 0.65;
  }
  50% {
    transform: scale(0.9);
    opacity: 1;
  }
  75% {
    transform: scale(1);
    opacity: 0.75;
  }
  100% {
    transform: scale(1.3);
    opacity: 0.5;
  }
}
@keyframes ymove {
  0% {
    // transform: translateY(0px);
    opacity: 1;
  }
  25% {
    // transform: translateY(2px);
    opacity: 0.5;
  }
  50% {
    // transform: translateY(0px);
    opacity: 0;
  }
  75% {
    // transform: translateY(5px);
    opacity: 0.5;
  }
  100% {
    // transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes rowup {
  0% {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    display: none;
  }
}
</style>



